* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  display: flex;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  justify-content: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition: all .3s;
}

.product-card__title {
  color: #060b35;
  cursor: pointer;
  margin: 0 0 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__image {
  align-items: center;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  padding-left: auto;
  padding-right: auto;
  display: block;
}

.product-card__code {
  color: #616070;
  margin: 0 0 17px;
  font-size: 10px;
  font-weight: 400;
}

.product-card__reviews-text {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-cost {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-style: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  transition: all .3s;
}

.product-card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
